// src/graphql/apollo-client.js
import { ApolloClient, InMemoryCache, createHttpLink, from } from '@apollo/client';
import { setContext } from '@apollo/client/link/context';
import { onError } from '@apollo/client/link/error';
import keycloak from '../auth/keycloak';

// Your Hasura GraphQL endpoint
const httpLink = createHttpLink({
  uri: 'https://reslobase-api.159.69.188.164.nip.io/v1/graphql', // Update this to your Hasura endpoint
});

// Authentication link - adds JWT token to requests
const authLink = setContext((_, { headers }) => {
  // Get the authentication token from Keycloak
  const token = keycloak.token;

  console.log('🔗 Apollo: Adding auth token to request');
  console.log('Token present:', !!token);

  return {
    headers: {
      ...headers,
      // Hasura expects Authorization header with Bearer token
      authorization: token ? `Bearer ${token}` : "",
      // Optional: Add custom headers for Hasura role-based access
      'x-hasura-role': 'user', // or get this from token claims
    }
  };
});

// Error link - handles authentication errors
const errorLink = onError(({ graphQLErrors, networkError, operation, forward }) => {
  if (graphQLErrors) {
    graphQLErrors.forEach(({ message, locations, path, extensions }) => {
      console.error('GraphQL error:', message);

      // Handle Hasura authentication errors
      if (extensions?.code === 'invalid-jwt' || extensions?.code === 'access-denied') {
        console.error('🔒 Authentication error - token may be expired');

        // Try to refresh token
        if (keycloak.authenticated) {
          keycloak.updateToken(70).then((refreshed) => {
            if (refreshed) {
              console.log('✅ Token refreshed, retrying request');
              // Retry the failed operation
              return forward(operation);
            } else {
              console.log('❌ Token refresh failed, redirecting to login');
              keycloak.login();
            }
          }).catch(() => {
            console.log('❌ Token refresh failed, redirecting to login');
            keycloak.login();
          });
        }
      }
    });
  }

  if (networkError) {
    console.error('Network error:', networkError);

    // Handle network errors (CORS, server down, etc.)
    if (networkError.statusCode === 401) {
      console.error('🔒 Unauthorized - check your Keycloak configuration');
    } else if (networkError.statusCode === 403) {
      console.error('🚫 Forbidden - check your Hasura permissions');
    }
  }
});

// Create Apollo Client
const client = new ApolloClient({
  link: from([
    errorLink,
    authLink,
    httpLink
  ]),
  cache: new InMemoryCache({
    // Optional: Configure cache policies for better performance
    typePolicies: {
      Query: {
        fields: {
          // Add your specific query field policies here
        }
      }
    }
  }),
  defaultOptions: {
    watchQuery: {
      errorPolicy: 'ignore',
    },
    query: {
      errorPolicy: 'all',
    },
  },
  // Enable developer tools in development
  connectToDevTools: process.env.NODE_ENV === 'development',
});

export default client;